  }
}

.pt_thing {
  filter: drop-shadow(2px 2px 3px #ccc);
  margin: 1rem;
  background: white;
  padding: 0.5rem;
  position: relative;
  animation: textMoveLeft 0.5s ease both 1;
  &::before {
    content: "";
    width: 0px;
    height: 0px;
    border-right: 10px solid white !important;
    left: -20px;
    //   background: red;
    border: 10px solid transparent;
    position: absolute;
  }
