    font-size: 0.75rem;
    padding: 0.5rem 0;
    border-bottom: 1px solid #f1f1f1;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    a {
      color: #5a5a5a;
      font-weight: 100;
    }
  }
  .msg {
    font-weight: 100;
    font-size: 0.88rem;
    color: #5a5a5a;
    margin: 0.5rem 0;
  }
  .img_box {
    display: flex;
    flex-wrap: wrap;
    .img {
      border: 2px dashed #f1f1f1;
      width: 5rem;
      height: 5rem;
      overflow: hidden;
      margin-right: 0.5rem;
      margin-bottom: 0.5rem;
      text-align: center;
      &:active {
        opacity: 0.7;
      }
      img {
        display: inline-block;
        height: 100%;
      }
    }
  }
}
@keyframes plan {
  100% {
    opacity: 1;
    transform: scaleY(1);
  }
}
@keyframes planP {
  25% {
    height: 30%;
  }
  50% {
    height: 60%;
  }
  75% {
    height: 85%;
  }
  100% {
    height: 100%;
  }
}
@keyframes textMoveLeft {
  0% {
    opacity: 0;
    transform: translateX(2rem);
  }
  100% {
    opacity: 1;
    transform: translateX(0rem);
  }
}
</style>


